import { createAppAuth } from '@octokit/auth-app';
import * as T from 'fp-ts/Either';
import * as TE from 'fp-ts/TaskEither';
import { Octokit } from 'octokit';
import isBase64 from 'is-base64';
const handleErrorReason = (reason) => new Error(String(reason));
const removeAtMark = (input) => input.replace(/^@/, '');
const parseRepositoryName = (name) => {
    const segments = name.split('/');
    if (segments.length !== 2 || !segments[0] || !segments[1]) {
        return T.left(new Error(`Repository name must be in the "owner/repo" format. ("${name}" is an invalid format)`));
    }
    return T.right([segments[0], segments[1]]);
};
const createGitHubRepository = TE.tryCatchK(async ({ rest, name }) => {
    const parsed = parseRepositoryName(name);
    if (T.isLeft(parsed)) {
        throw parsed.left;
    }
    const defaults = {
        owner: parsed.right[0],
        repo: parsed.right[1],
    };
    const { data: repo, ...test } = await rest.repos.get(defaults);
    console.dir({ repo, ...test }, { depth: null });
    if (1 > 0) {
        throw new Error('test');
    }
    return {
        owner: defaults.owner,
        name: defaults.repo,
        createBranch: TE.tryCatchK(async (name) => {
            // get base branch
            const { data: base } = await rest.git.getRef({
                ...defaults,
                ref: `heads/${repo.default_branch}`,
            });
            // update exisiting branch
            const updated = await TE.tryCatch(async () => {
                const { data } = await rest.git.updateRef({
                    ...defaults,
                    ref: `heads/${name}`,
                    sha: base.object.sha,
                    force: true,
                });
                return data;
            }, handleErrorReason)();
            if (T.isRight(updated)) {
                return {
                    name,
                    sha: updated.right.object.sha,
                };
            }
            // create branch
            const { data: ref } = await rest.git.createRef({
                ...defaults,
                ref: `refs/heads/${name}`,
                sha: base.object.sha,
            });
            return {
                name,
                sha: ref.object.sha,
            };
        }, handleErrorReason),
        deleteBranch: TE.tryCatchK(async (name) => {
            await rest.git.deleteRef({
                ...defaults,
                ref: `heads/${name}`,
            });
        }, handleErrorReason),
        commit: TE.tryCatchK(async ({ parent, branch, files, message }) => {
            // create tree
            const { data: tree } = await rest.git.createTree({
                ...defaults,
                base_tree: parent,
                tree: files.map((file) => ({
                    mode: '100644',
                    path: file.path,
                    content: file.content,
                })),
            });
            // commit
            const { data: commit } = await rest.git.createCommit({
                ...defaults,
                tree: tree.sha,
                message,
                parents: [parent],
            });
            // apply to branch
            await rest.git.updateRef({
                ...defaults,
                ref: `heads/${branch}`,
                sha: commit.sha,
                force: true,
            });
            return commit;
        }, handleErrorReason),
        compareCommits: TE.tryCatchK(async (base, head) => {
            const { data: diff } = await rest.repos.compareCommits({
                ...defaults,
                base,
                head,
            });
            return diff.files;
        }, handleErrorReason),
        findExistingPullRequestByBranch: TE.tryCatchK(async (branch) => {
            const { data: prs } = await rest.pulls.list({
                ...defaults,
                state: 'open',
                head: `${defaults.owner}:${branch}`,
            });
            return prs[0] ?? null;
        }, handleErrorReason),
        closePullRequest: TE.tryCatchK(async (number) => {
            await rest.pulls.update({
                ...defaults,
                pull_number: number,
                state: 'closed',
            });
        }, handleErrorReason),
        createOrUpdatePullRequest: TE.tryCatchK(async ({ title, body, number, branch }) => {
            if (number !== null && number !== undefined) {
                const { data } = await rest.pulls.update({
                    ...defaults,
                    pull_number: number,
                    title,
                    body,
                });
                return data;
            }
            else {
                const { data } = await rest.pulls.create({
                    ...defaults,
                    base: repo.default_branch,
                    head: branch,
                    title,
                    body,
                });
                return data;
            }
        }, handleErrorReason),
        addPullRequestLabels: TE.tryCatchK(async (number, labels) => {
            await rest.issues.addLabels({
                ...defaults,
                issue_number: number,
                labels,
            });
        }, handleErrorReason),
        addPullRequestReviewers: TE.tryCatchK(async (number, original) => {
            const [reviewers, team_reviewers] = original.reduce((acc, cur) => {
                const match = cur.match(/^team:(.+)$/);
                if (match !== null) {
                    acc[1].push(removeAtMark(match[1]));
                }
                else {
                    acc[0].push(removeAtMark(cur));
                }
                return acc;
            }, [[], []]);
            await rest.pulls.requestReviewers({
                ...defaults,
                pull_number: number,
                reviewers,
                team_reviewers,
            });
        }, handleErrorReason),
        addPullRequestAssignees: TE.tryCatchK(async (number, assignees) => {
            await rest.issues.addAssignees({
                ...defaults,
                issue_number: number,
                assignees: assignees.map((a) => removeAtMark(a)),
            });
        }, handleErrorReason),
    };
}, handleErrorReason);
export const createGitHub = (inputs) => {
    const octokit = inputs.github_token !== null
        ? new Octokit({
            auth: inputs.github_token,
            baseUrl: inputs.github_api_url,
        })
        : new Octokit({
            authStrategy: createAppAuth,
            auth: {
                appId: inputs.github_app_id,
                installationId: inputs.github_app_installation_id,
                privateKey: isBase64(inputs.github_app_private_key)
                    ? Buffer.from(inputs.github_app_private_key, 'base64').toString('utf8')
                    : inputs.github_app_private_key,
            },
            baseUrl: inputs.github_api_url,
        });
    const { rest } = octokit;
    return {
        initializeRepository: TE.tryCatchK(async (name) => {
            const repo = await createGitHubRepository({
                rest,
                name,
            })();
            if (T.isLeft(repo)) {
                throw repo.left;
            }
            return repo.right;
        }, handleErrorReason),
    };
};
